import React, { Component } from "react";
import "./styles.css";

export default class Tabs extends Component {
  state = {
    tabs: []
  };

  componentDidMount() {
    this.getComponents();
  }

  getComponents = () => {
    const tabs = [
      { id: "1", name: "todos", img: require("../../assets/all.svg") },
      { id: "4", name: "cupons", img: require("../../assets/coupon.svg") },
      { id: "2", name: "promoções", img: require("../../assets/promo.svg") },
      { id: "3", name: "amostras", img: require("../../assets/sample.svg") }
    ];
    this.setState({ tabs });
  };

  render() {
    const { tabs } = this.state;
    return (
      <div className="tabs-container">
        {tabs.map(tab => (
          <div className="tab-item" key={tab.id}>
            <div
              className="item-img"
              style={{ backgroundImage: "url(" + tab.img + ")" }}
            />
            <p>{tab.name}</p>
          </div>
        ))}
      </div>
    );
  }
}
