import React, { Component } from "react";
import api from "../../services/api";
import "./styles.css";
export default class Main extends Component {
  state = {
    products: [],
    listInfo: {},
    page: 1,
    loading: false,
    requestError: false
  };

  componentDidMount() {
    this.loadProducts();
  }

  loadProducts = async (page = 1) => {
    this.setState({ loading: true });
    await api.get(`/products?page=${page}`).then(
      res => {
        const { docs, ...productInfo } = res.data;
        let { products } = this.state;
        products = products.concat(docs);
        this.setState({
          products,
          listInfo: productInfo,
          page,
          loading: false,
          requestError: false
        });
      },
      err => {
        this.setState({ loading: false, requestError: true });
      }
    );
  };

  nextPage = () => {
    const { page, listInfo } = this.state;
    if (page === listInfo.pages) return;
    const currentPage = page + 1;
    this.loadProducts(currentPage);
  };

  render() {
    const { products, loading, page, listInfo, requestError } = this.state;
    return (
      <div>
        {requestError && (
          <div className="request-error">
            <h1>Sem conexão à internet</h1>
            <p>
              Verifique se você está conectado à internet e,
              <br />
              após se conectar, toque no botão a seguir
            </p>
            <button onClick={this.loadProducts}>Recarregar</button>
          </div>
        )}
        {!requestError && (
          <div className="product-list">
            {products.map(product => (
              <article key={product._id}>
                <div className="product-img" />
                <div className="product-data">
                  <strong>Samsung Galaxy S9</strong>
                  <p>vendido por Americanas</p>
                  <p>
                    de R$1300 por <strong>R$1100</strong>
                  </p>
                  {/* <a href="https://google.com">Acessar</a> */}
                </div>
                <div className="product-type">
                  <div className="type-icon" />
                  <p>cupom</p>
                </div>
              </article>
            ))}
            <div className="navigation">
              {loading && <div className="loading-image" />}
              {!loading && page !== listInfo.pages && (
                <button onClick={this.nextPage}>Carregar mais promoções</button>
              )}
              {!loading && page === listInfo.pages && (
                <p>Não há mais promoções</p>
              )}
            </div>
          </div>
        )}
      </div>
    );
  }
}
